:root {
    --clr-black: #000;
    --clr-dark: #1a1f16;
    --clr-grey: #60695c;
    --clr-background: #ededed;
    --clr-white: #ffffff;
    --clr-green: #12805d;
}

body {
    font-family: "Open Sans", sans-serif;
    font-size: 0.75em;
    color: #1a1f16;
}

@media (min-width: 400px) {
    body {
        font-size: 1em;
    }
}

.nav {
    margin: 0 auto;
    padding: 1em 1em;
    max-width: 1400px;
    background-color: #ededed;
    color: var(--clr-dark);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 0.5em;
}

@media (min-width: 576px) {
    .nav {
        flex-direction: row;
        gap: 1em;
    }
}

.master-container {
    margin: 0 auto;
    padding: 1em 0;
    max-width: 1400px;
    background-color: #ededed;
    color: var(--clr-dark);
    display: flex;
    flex-direction: column-reverse;
}

.container-left {
    padding: 0 1em;
}

.container-right {
    padding: 0 1em 1em 1em;
    /* border-bottom: 2px solid var(--clr-dark); */
}

@media (min-width: 576px) {
    .master-container {
        flex-direction: row;
    }

    .container-left {
        flex: 3;
        min-height: calc(100vh - 2em);
        border-right: 4px solid var(--clr-dark);
        /* order: 1; */
    }

    .container-right {
        flex: 1;
        border: none;
        /* order: 0; */
    }
}

/* PRODUCTS */
.product-list {
    margin: 0 auto;
    width: auto;
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-evenly; */
    gap: 1em;
    border: 1px dashed blue;
}

.product-item {
    /* width: calc((100% - 2em) / 2); */
    padding: 1em;
    width: 100%;
    /* height: 10em; */
    /* background-color: aqua; */
    border: 1px solid var(--clr-white);
    border-radius: 1.5em;
    cursor: pointer;
}

@media (min-width: 400px) {
    .product-item {
        width: calc((100% - 1em) / 2);
    }
}

@media (min-width: 768px) {
    .product-item {
        width: calc((100% - 2em) / 3);
    }
}

@media (min-width: 992px) {
    .product-item {
        width: calc((100% - 3em) / 4);
    }
}

.product-item:hover {
    /* border: 1px solid var(--clr-background); */
    background-color: var(--clr-white);
    /* box-shadow: 5px 5px 5px var(--clr-white); */
}

.product-item__img {
    width: 100%;
    border-radius: 1em;
}

.product-item__content_desc {
    margin-top: 1em;
}

.product-item__content_part-number {
    margin-top: 0.5em;
    font-size: 0.8em;
    color: var(--clr-grey);
}

.product-item__content_price-block {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.product-item__content_price-btn {
    font-family: inherit;
    padding: 0.5em;
    color: var(--clr-white);
    background-color: var(--clr-black);
    border: 1px solid var(--clr-grey);
    border-radius: 0.7em;
    cursor: pointer;
}

.product-item__content_price-btn:hover {
    background-color: var(--clr-grey);
    border: 1px solid var(--clr-black);
}
/* END of PRODUCTS */

/* CART LINKS */
.cart-links-title {
    font-size: 1.5em;
    font-weight: 700;
}

.cart-links-wrapper {
    margin-top: 1em;
    width: 100%;
    display: flex;
    /* justify-content: space-evenly; */
    flex-wrap: wrap;
    gap: 1em;
    border: 1px dashed red;
}

.cart-link-btn {
    font-family: inherit;
    width: calc((100% - 2em) / 3 - 2px);
    aspect-ratio: 1 / 1;
    background-size: cover;
    background-position: center;
    /* display: block; */
    border: 1px solid var(--clr-background);
    border-radius: 1em;
    cursor: pointer;
}

@media (min-width: 400px) {
    .cart-link-btn {
        width: calc((100% - 3em) / 4 - 2px);
    }
}

@media (min-width: 576px) {
    .cart-link-btn {
        width: calc((100% - 1em) / 2 - 2px);
    }
}

@media (min-width: 768px) {
    .cart-link-btn {
        width: calc((100% - 2em) / 3 - 2px);
    }
}

@media (min-width: 992px) {
    .cart-link-btn {
        width: calc((100% - 3em) / 4 - 2px);
    }
}

.cart-link-btn:hover {
    border: 1px solid var(--clr-black);
    /* box-shadow: 5px 5px var(--clr-white); */
}

.cart-total-amount-wrapper {
    margin-top: 1em;
    width: 100%;
    display: flex;
    justify-content: center;
    border: 1px dashed red;
}

.cart-total-amount {
    font-size: 0.75em;
}

.cart-checkout-btn-wrapper,
.cart-goto-btn-wrapper {
    margin-top: 1em;
    width: 100%;
    display: flex;
    justify-content: center;
    border: 1px dashed red;
}

.cart-checkout-btn,
.cart-goto-btn {
    font-family: inherit;
    margin-inline: auto;
    padding: 0.5em 1em;
    color: var(--clr-white);
    background-color: var(--clr-black);
    border: 1px solid var(--clr-grey);
    border-radius: 0.7em;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
}

.cart-checkout-btn:hover,
.cart-goto-btn:hover {
    background-color: var(--clr-grey);
    border: 1px solid var(--clr-black);
}

.cart-checkout-btn img,
.cart-goto-btn img {
    height: 1.5em;
}
/* END of CART LINKS */

/* ORDERS LIST */
.order-list-title {
    margin-top: 1em;
    font-size: 1.5em;
    font-weight: 700;
}

.orders-list {
    margin-top: 1em;
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-between; */
    gap: 1em;
    border: 1px dashed red;
}

.order-number-btn {
    font-family: inherit;
    border: none;
    cursor: pointer;
}

.order-number-btn:hover {
    font-weight: 700;
    text-decoration: underline;
}
/* END of ORDERS LIST */

/* PRODUCT CARD*/
.goto-products-btn {
    width: max-content;
    font-family: inherit;
    border: none;
    cursor: pointer;
}

.goto-products-btn:hover {
    font-weight: 700;
    text-decoration: underline;
}

.product-card {
    display: flex;
    flex-direction: column;
    gap: 1em;
    border: 1px dashed blue;
}

.product-card__top-wrapper {
    display: flex;
    flex-direction: column;
    gap: 1em;
}

.product-card__img-wrapper {
    /* flex: 1; */
    border: 1px dashed red;
}

.product-card__img {
    width: 100%;
    border-radius: 1em;
}

.product-card__summary-wrapper {
    /* flex: 2; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1em;
}

/* CHANGE FLEX DIRECTION FOR PRODUCT IMG AND DESCRIPTION */
@media (min-width: 300px) {
    .product-card__top-wrapper {
        flex-direction: row;
    }

    .product-card__img-wrapper {
        flex: 1;
    }

    .product-card__summary-wrapper {
        flex: 2;
    }
}

.product-card__product-name {
    font-size: 1.75em;
    font-weight: 700;
}

.product-card__support-txt {
    margin: 0.5em 0 0.3em;
    font-size: 0.75em;
}

.product-card__add-to-cart-btn {
    font-family: inherit;
    margin-inline: auto;
    padding: 0.5em 1em;
    color: var(--clr-white);
    background-color: var(--clr-black);
    border: 1px solid var(--clr-grey);
    border-radius: 0.7em;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
}

.product-card__add-to-cart-btn img {
    height: 1.5em;
}

.product-card__add-to-cart-btn:hover {
    background-color: var(--clr-grey);
    border: 1px solid var(--clr-black);
}

.product-card__bottom-wrapper {
    padding-top: 1em;
    border-top: 2px solid var(--clr-grey);
}

.product-card__description {
    margin-bottom: 0.5em;
    font-size: 1.25em;
}
/* END of PRODUCT CARD */

/* CART WRAPPER */
.cart-wrapper {
    display: flex;
    flex-direction: column;
    gap: 1em;
    border: 1px dashed red;
}

.cart-title {
    font-size: 1.5em;
    font-weight: 700;
}

.cart-items-wrapper {
    display: flex;
    flex-direction: column;
    gap: 1em;
    border: 1px dashed green;
}

.cart-item {
    padding: 1em;
    border-radius: 1.5em;
    display: flex;
    flex-direction: column;
    gap: 1em;
    background-color: var(--clr-white);
}

/* .cart-item__img-wrapper {
    flex: 1;
} */

.cart-item__img {
    width: 100%;
    border-radius: 1em;
}

/* .cart-item__summary-wrapper {
    flex: 3;
} */

@media (min-width: 300px) {
    .cart-item {
        flex-direction: row;
    }

    .cart-item__img-wrapper {
        flex: 1;
    }

    .cart-item__summary-wrapper {
        flex: 3;
    }
}

.cart-item__summary {
    display: flex;
    flex-direction: column;
    gap: 1em;
}

.cart-item__element-wrapper {
    display: flex;
    flex-direction: row;
    gap: 1em;
    align-items: baseline;
}

.cart-item__price-qty {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.cart-element__qty-mod_wrapper {
    display: flex;
    flex-direction: row;
    gap: 0.5em;
    align-items: center;
}

.cart-item__product-name {
    font-size: 1.5em;
    font-weight: 700;
}

.cart-item__support-txt {
    /* margin: 0.5em 0 0.3em 0; */
    font-size: 0.75em;
}

.cart-element__qty-mod_deduct-btn,
.cart-element__qty-mod_add-btn {
    border: none;
    background-color: var(--clr-white);
    font-family: inherit;
    font-size: inherit;
    cursor: pointer;
}

.cart-element__qty-mod_deduct-btn:hover,
.cart-element__qty-mod_add-btn:hover {
    font-weight: 700;
    color: var(--clr-green);
}

/* END of CART WRAPPER */
